@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  /* Colors */
  --white: #FFFFFF;
  --gold: #E8B500;
  --gray: #C4C4C4;
  --black: #242424;
  --transparent-black: rgba(36,36,36,0.7);
  --transparent-gray: rgba(196,196,196,0.7);
  --facebook-color: #4267B2;
  --twitter-color: #1DA1F2;

  /* Font Size */
  --title-font-size: 50px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

html {
  scroll-behavior: smooth;
}

.highlight-letter {
  color: #E8B500;
  color: var(--gold);
  font-size: 75px;
}

.title {
  font-size: 50px;
  font-size: var(--title-font-size);
  text-align: center;
  position: relative;
  padding-bottom: 10px;
  color: #FFFFFF;
  color: var( --white );
}

.title::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 150px;
  height: 5px;
  background: #FFFFFF;
  background: var(--white);
}

.container {
  max-width: 1366px;
  margin: auto;
}


/* Animation Class */

/* Tech Stacks Section */
.tech-stacks .stack.popup-from-left {
  opacity: 1;
  margin-left: 0 !important;
}

.tech-stacks .stack.popup-from-right {
  opacity: 1;
  margin-right: 0 !important;
}

/* Project Section */
.popup {
  -webkit-transform: scale(1) !important;
          transform: scale(1) !important;
}


@media (max-width: 767px) {

  .highlight-letter {
    font-size: 55px;
  }

}
.App {
	background: url(/Portfolio/static/media/bg.af4ba14f.jpg) no-repeat;
	background-size: 100%;
	background-attachment: fixed;
}

@media (max-width: 1024px) {
	.App {
		background-repeat: repeat;
		background-size: cover !important;
	}
}
.header {
	height: 100vh;
	max-height: 1080px;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.top-gold-bar {
	position: absolute;
	top: 0;
	height: 10px;
	width: 100%;
	display: block;
	background: var(--gold);
}

.content {
	width: 1200px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-flow: row wrap;
}

.me {
	width: 600px;
	color: var(--white);
}

.me .name {
	font-size: 50px;
	margin-bottom: -15px;
}

.me .developer {
	font-size: 35px;
}

.me .description {
	font-size: 25px;
	margin-top: 30px;
	margin-bottom: 45px;
}

.me .see-projects-button {
	display: inline-block;
	text-decoration: none;
	background: var(--gold);
	color: var(--black);
	font-size: 19px;
	padding: 12px 30px;
	border-radius: 50px;
}

.header .image-container {
	width: 400px;
	height: 400px;
	border-radius: 50%;
	overflow: hidden;
}

.image-container img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}


@media (max-width: 767px) {

	.content {
		justify-content: center;
	}

	.me {
		width: 100%;
		order: 2;
	}

	.me .name {
		font-size: 40px;
		margin-bottom: 0;
		text-align: center;
	}

	.me .developer {
		font-size: 25px;
		text-align: center;
		font-weight: 700;
	}

	.me .description {
		font-size: 20px;
		text-align: center;
	}

	.me .see-projects-button {
		font-size: 16px;
		display: block;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		margin: auto;
	}

	.header .image-container {
		width: 250px;
		height: 250px;
		order: 1;
	}

}


@media (min-width: 768px) and (max-width: 1024px) {

	.header {
		height: 100vh;
	}

	.content {
		justify-content: center;
	}

	.me {
		width: 100%;
		order: 2;
	}

	.me .name,
	.me .developer,
	.me .description {
		text-align: center;
	}

	.me .see-projects-button {
		display: block;
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		margin: auto;
	}

	.me .image-container {
		order: 1;
	}

}
.tech-stacks {
	padding: 70px;
}

.tech-stacks .stacks {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0 50px;
	position: relative;
	margin: 200px 0;
}
.timeline {
	display: inline-block;
	position: absolute;
	top: 25px;
	left: 50%;
	-webkit-transform-origin: top;
	        transform-origin: top;
	width: 5px;
	height: 1px;
	background: white;
	transition: all .3s;
}

.tech-stacks .stack {
	width: 400px;
	margin-bottom: 200px;
	opacity: 0;
	transition: all 1s;
	color: var(--white);
}
.tech-stacks .stack:nth-child(even) {
	align-self: flex-start;
	margin-left: -100px;
}
.tech-stacks .stack:nth-child(odd) {
	align-self: flex-end;
	margin-right: -100px;
}
.tech-stacks .stack:last-child {
	margin-bottom: 0;
}
.tech-stacks .stack-title {
	background: var(--gold);
	text-align: center;
	padding: 10px;
	color: var(--black);
	font-size: 19px;
	font-weight: 500;
	border-radius: 30px;
}

.tech-stacks .stack .dot {
	content: '';
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	display: inline-block;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	border: 5px solid var( --white );
	background: var( --gold );
}

.languages {
	padding: 10px;
	height: 150px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-flow: row wrap;
	background: var(--transparent-black);
	margin-top: 20px;
	border-radius: 30px;
}

.lang {
	display: inline-block;
	width: calc( 100% / 3 );
	text-align: center;
	font-size: 19px;
}


@media (max-width: 767px) {

	.tech-stacks {
		padding: 0 20px;
		margin: 100px auto;
	}

	.tech-stacks .stacks {
		margin: 100px 0;
		padding: 0;
	}

	.tech-stacks .stack {
		width: 90%;
	}
	.tech-stacks .stack:nth-child(even) {
		align-self: flex-end;
		margin-left: 0;
	}

	.tech-stacks .stack:nth-child(odd) {
		align-self: flex-end;
		margin-right: 0;
	}

	.tech-stacks .stack .dot {
		left: 0;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}

	.timeline {
		z-index: 0;
		left: 12px;
		-webkit-transform: translateX(0) scaleY( var(--timeline-height) );
		        transform: translateX(0) scaleY( var(--timeline-height) );
	}

}


@media (min-width: 768px) and (max-width: 1024px) {

	.tech-stacks {
		padding: 70px 20px;
	}

	.tech-stacks .stacks {
		padding: 0;
		overflow: hidden;
	}

	.tech-stacks .stack {
		width: 350px;
	}

	.tech-stacks .stack:nth-child(even) {
		/* align-self: flex-start;
		margin-left: -200px; */
	}

	.tech-stacks .stack:nth-child(odd) {
		/* align-self: flex-end;
		margin-right: -200px; */
	}

}
.my-projects .projects {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-flow: row wrap;
	padding: 200px 50px;
}

.project {
	width: 350px;
	background: var( --black );
	padding: 10px;
	position: relative;
	-webkit-transform: scale(0);
	        transform: scale(0);
	transition: all .5s;
}

.project::after {
	content: '';
	display: inline-block;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 8px;
	background: var( --gold );
}

.project .image-container {
	width: 100%;
	height: 250px;
	position: relative;
}

.project .image-container:hover > img {
	object-fit: cover;
}

.project .image-container:hover > .on-hover {
	opacity: 1;
}

.project .image-container .on-hover {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: var( --transparent-gray );
	transition: all .5s;
	opacity: 0;
}

.project .image-container .on-hover a {
	background: var( --gold );
	text-decoration: none;
	color: var( --black );
	padding: 8px 20px;
	border-radius: 30px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.project .name {
	font-size: 25px;
	text-align: center;
	padding: 20px 0;
	color: var(--white);
	font-weight: 500;
}

.project .description {
	color: var(--white);
	text-align: center;
	font-weight: 300;
}

.project .stacks {
	display: flex;
	justify-content: space-evenly;
	flex-flow: row wrap;
	margin: 30px 10px 30px;
}

.project .stacks .stack {
	color: var( --white );
	font-weight: 500;
}

@media (max-width: 767px) {

	.my-projects .projects {
		padding: 100px 20px;
		justify-content: center;
	}

	.project {
		margin-bottom: 20px;
	}

	.project:last-child {
		margin-bottom: 0;
	}

}

@media (min-width: 768px) and (max-width: 1024px) {

	.my-projects .projects {
		justify-content: center;
		flex-flow: column wrap;
	}

	.project {
		margin-bottom: 10px;
	}

	.project:last-of-type {
		margin-bottom: 0;
	}

}

.form {
	margin: 200px auto;
	background: var( --black );
	width: 800px;
	padding: 50px;
}

.fields {
	display: flex;
	justify-content: space-between;
	flex-flow: row wrap;
}

.input, .textarea {
	width: 47%;
}

.field {
	margin-bottom: 30px;
}

.field:last-child {
	margin-bottom: 0;
}

.field label,
.field input,
.textarea label,
.textarea textarea {
	display: block;
}

.field label, .textarea label {
	margin-bottom: 10px;
	font-size: 19px;
	color: var( --white );
}

.field input {
	color: var( --black );
	padding: 15px;
	border-radius: 10px;
	border: none;
	outline: none;
	width: 100%;
}

.textarea textarea {
	color: var( --black );
	width: 100%;
	height: 80%;
	border-radius: 10px;
	border: none;
	outline: none;
	padding: 15px;
	resize: none;
}

.form button[type="submit"] {
	background: var( --gold );
	color: var( --black );
	border: none;
	padding: 12px 50px;
	border-radius: 50px;
	font-size: 19px;
	display: block;
	margin: 60px auto 0;
	cursor: pointer;
}


@media (max-width: 767px) {

	.form {
		padding: 30px 20px;
		width: 90%;
		margin: 100px auto;
	}

	.input, .textarea {
		width: 100%;
	}

	.field {
		margin-bottom: 20px;
	}

	.field:last-child {
		margin-bottom: 20px;
	}

	.field label, .textarea label {
		font-size: 16px;
	}

	.field input, .textarea textarea {
		padding: 13px;
	}

	.textarea {
		height: 150px;
	}

	.textarea textarea {
		height: 100%;
	}

	.form button[type="submit"] {
		font-size: 16px;
	}

}


@media (min-width: 768px) and (max-width: 1024px) {

	.form {
		width: 700px;
	}

}
.footer {
	padding: 40px 50px;
	font-size: 19px;
}

.footer .container {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.copyright {
	color: var( --white );
}

.social-medias a {
	margin-left: 30px;
}

.social-medias a:first-child {
	margin-left: 0;
}

.facebook-icon {
	color: var( --facebook-color );
}

.twitter-icon {
	color: var( --twitter-color );
}


@media (max-width: 767px) {

	.footer {
		padding: 20px;
		font-size: 16px;
	}

	.copyright {
		width: 70%;
	}

	.social-medias a {
		margin-left: 10px;
		font-size: 19px;
	}

}
